import React from "react";

import "./CoursList.css";
import CourItem from "./CourItem";

const matiere = [
  "Mathématique",
  "Science Naturelle",
  "Histoire",
  "Physique",
  "Français",
  "Science Islamique",
];

const CoursList = (props) => {
  return matiere.map((m, key) => {
    return (
      <CourItem
        key={m}
        className={`bg-${key % 4}`}
        trimestre="1er Trimestre"
        lessonsCount={30}
        matiere={m}
        showVideos={props.showVideos}
      />
    );
  });
};

export default CoursList;
